"use client";

import React, { useState } from 'react';
import Link from 'next/link';
import Navbar from '../../components/navbar';
import Footer from '../../components/footer';

// Data dummy untuk dokumentasi
const galleryItems = [
  { id: 1, title: "Upacara Pembukaan", category: "Acara Utama" },
  { id: 2, title: "Pertandingan Basket", category: "Basket" },
  { id: 3, title: "Lomba Renang", category: "Akuatik" },
  { id: 4, title: "Pertandingan Voli", category: "Voli" },
  { id: 5, title: "Penyerahan Medali", category: "Penghargaan" },
  { id: 6, title: "Atlet Berlari", category: "Atletik" },
  { id: 7, title: "Latihan Tim", category: "Persiapan" },
  { id: 8, title: "Sesi Pelatihan", category: "Pelatihan" },
  { id: 9, title: "Wawancara Atlet", category: "Media" },
  { id: 10, title: "Pertandingan Tenis", category: "Tenis" },
  { id: 11, title: "Pertandingan Bulutangkis", category: "Bulutangkis" },
  { id: 12, title: "Penutupan", category: "Acara Utama" },
  { id: 13, title: "Pertandingan Sepak Bola", category: "Sepak Bola" },
  { id: 14, title: "Pertandingan Panahan", category: "Panahan" },
  { id: 15, title: "Pertandingan Silat", category: "Bela Diri" },
];

const DocumentationPage = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 9;
  
  // Hitung data yang ditampilkan per halaman
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = galleryItems.slice(indexOfFirstItem, indexOfLastItem);
  const totalPages = Math.ceil(galleryItems.length / itemsPerPage);

  return (
    <div style={{ 
      minHeight: '100vh', 
      display: 'flex', 
      flexDirection: 'column',
      backgroundColor: 'var(--background)',
      color: 'var(--foreground)'
    }}>
      <Navbar />
      
      <main className="flex-grow container mx-auto px-4 py-8">
        <div className="max-w-6xl mx-auto">
          <h1 className="text-center mb-8" style={{ 
            fontSize: 'var(--font-size-xlarge)',
            fontWeight: 'bold',
            color: 'var(--color-primary)'
          }}>
            DOKUMENTASI PORPROV JATIM 2025
          </h1>
          
          <div className="mb-8 text-center">
            <p style={{ 
              fontSize: 'var(--font-size-medium)',
              color: 'var(--color-gray-600)',
              maxWidth: '800px',
              margin: '0 auto'
            }}>
              Kumpulan momen berharga dari pelaksanaan Pekan Olahraga Provinsi Jawa Timur 2025
            </p>
          </div>
          
          {/* Grid Dokumentasi */}
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 mb-12">
            {currentItems.map((item) => (
              <div 
                key={item.id} 
                className="rounded-xl overflow-hidden shadow-lg transform transition-transform duration-300 hover:scale-[1.02]"
                style={{ 
                  backgroundColor: 'var(--color-gray-100)',
                  border: '1px solid var(--color-gray-200)'
                }}
              >
                <div className="h-48 flex items-center justify-center" style={{ backgroundColor: 'var(--color-gray-200)' }}>
                  <div className="text-center p-4">
                    <div className="bg-gray-300 border-2 border-dashed rounded-xl w-32 h-32 mx-auto" />
                    <p className="mt-4 font-medium" style={{ color: 'var(--color-gray-700)' }}>{item.title}</p>
                    <span className="text-sm" style={{ color: 'var(--color-primary)' }}>{item.category}</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
          
          {/* Pagination */}
          <div className="flex justify-center items-center space-x-2 mb-8">
            <button 
              onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
              disabled={currentPage === 1}
              className="px-4 py-2 rounded-md" 
              style={{
                backgroundColor: currentPage === 1 ? 'var(--color-gray-200)' : 'var(--color-primary)',
                color: currentPage === 1 ? 'var(--color-gray-600)' : 'white',
                cursor: currentPage === 1 ? 'not-allowed' : 'pointer'
              }}
            >
              Previous
            </button>
            
            {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
              <button
                key={page}
                onClick={() => setCurrentPage(page)}
                className={`px-4 py-2 rounded-md ${currentPage === page ? 'font-bold' : ''}`}
                style={{
                  backgroundColor: currentPage === page ? 'var(--color-primary)' : 'var(--color-gray-100)',
                  color: currentPage === page ? 'white' : 'var(--color-gray-800)'
                }}
              >
                {page}
              </button>
            ))}
            
            <button 
              onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
              disabled={currentPage === totalPages}
              className="px-4 py-2 rounded-md" 
              style={{
                backgroundColor: currentPage === totalPages ? 'var(--color-gray-200)' : 'var(--color-primary)',
                color: currentPage === totalPages ? 'var(--color-gray-600)' : 'white',
                cursor: currentPage === totalPages ? 'not-allowed' : 'pointer'
              }}
            >
              Next
            </button>
          </div>
        </div>
      </main>
      
      <Footer />
    </div>
  );
};

export default DocumentationPage;